import searchInfo from "components/common/searchInfo";
import axios from "axios";
export default {
  data() {
    const name = window.localStorage.getItem("userName")
    const myId = window.localStorage.getItem("myId")
    // const acceptbtn =  window.localStorage.getItem('acceptbtn')
    return {
      pageName: "人脉",
      name,
      friends: [], //好友列表
      friendReq: false,
      myId,
      addFriend: [],
      friendInfos:[],
      myAdd:[],
      // acceptbtn
    };
  },
  mounted() {
    // 查找加我好友的人
    axios.get('http://121.42.177.97:8080/red/user/find_people.do?user_id=' + this.myId)
      .then(res => {
        console.log(res.data)
        for(var val of res.data.data){
          // console.log(val.user_id)
          this.addFriend.push(val.user_id);
        }
        if (res.data.data.length !== 0) {
          this.friendReq = true;
          this.getFriendInfo()
        }
      })
      .catch(res => {
        throw res
      })
    // 查询我添加过好友的人
    axios.get('http://121.42.177.97:8080/red/user/no_friend.do?user_id=' + this.myId)
      .then(res => {
        this.myAdd = res.data.data
      })
      .catch(res => {
        throw res
      })
  },
  components: {
    searchInfo
  },
  methods: {
    // 获取好友信息
    getFriendInfo(params){
      for(var friendId of this.addFriend){
        axios.get('http://121.42.177.97:8080/red/user/find_id.do?friend_id=' + friendId)
        .then(res => {
          // console.log(res.data.data[0]);
          this.friendInfos.push(res.data.data[0]);
          if (res.data.data.length !== 0) {
            this.friendReq = true;
          }
        })
        .catch(res => {
          throw res;
        })
      }
    },
    // 接受添加好友
    accept(params){
      axios
      .get('http://121.42.177.97:8080/red/user/add_me_friend.do?user_id=' + this.myId + '&' + 'friend_id=' + params)
      .then(res => {
        if(res.data.data === '不能这样添加好友'){
          // this.acceptbtn = true
          window.localStorage.setItem("acceptbtn", this.acceptbtn)
        } else {
          this.$router.push({ path: "/contact" })
        }
      })
      .catch(res => {
        throw res
      })
    }
  }
}



