
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import axios from 'axios'
export default {
data () {
const name =  window.localStorage.getItem('userName')
return {
name,
results: [],
value: '',
friend: {},
userId: window.localStorage.getItem('userId') || ''
}
},
components: {
Search,
Group
},
methods: {
getResult (val) {
console.log('on-change', val)
this.results = val ? getResult(this.value) : []
},
onSubmit () {
this.$refs.search.setBlur()
},
onFocus () {
console.log('on focus')
},
searchPerson () {
var telReg = /^1[3|4|5|7|8][0-9]{9}$/
if (!telReg.test(this.value)) {
this.$vux.toast.show({
type: 'cancel',
text: '手机格式不正确'
})
return 0
} else {
axios
.post('http://121.42.177.97:8080/red/user/findfriend.do?name=' + this.value)
.then(res => {
console.log(res.data)
if(res.data.message === '查不到该联系人'){
this.$vux.toast.show({
type: 'cancel',
text: '查不到该联系人'
})
} else {
this.friend = res.data.data
window.localStorage.setItem("friendName",res.data.data.user_name)
window.localStorage.setItem("userId",res.data.data.user_id)
window.localStorage.setItem("nickName", res.data.data.nick_name)
this.$router.push({
name: 'userDetails',
params: {
id: res.data.data.user_id
}
})
}
})
.catch(res => {
throw res
})
}
}
}
}



